<template>
  <div class="group">
    <van-form @submit="onSubmit">
      <van-field v-model="dataForm.cooperationName"
                 name="cooperDiscount"
                 label="单位名称"
                 placeholder="请输入单位名称"
                 :rules="[{ required: true, message: '请填写请输入单位名称' }]" />
      <van-field v-model="dataForm.cooperationSn"
                 name="cooperationSn"
                 label="单位编号"
                 placeholder="请输入单位编号"
                 :rules="[{ required: true, message: '请填写请输入单位编号' }]" />

      <van-field v-model="dataForm.contact"
                 name="contact"
                 label="联系人"
                 placeholder="请输入联系人"
                 :rules="[{ required: true, message: '请填写联系人' }]" />
      <van-field v-model="dataForm.phone"
                 name="phone"
                 label="联系电话"
                 maxlength='11'
                 placeholder="请输入联系电话"
                 :rules="[{ required: true, message: '请填写联系电话' },{ validator:validator, message: '请填写正确的联系电话' }]" />
      <van-field v-model="dataForm.email"
                 name="email"
                 label="电子邮箱"
                 placeholder="请输入电子邮箱"
                 :rules="[{ validator:validatorEmail, message: '请填写正确格式的邮箱' }]" />

      <van-field style="margin-top: 12px"
                 v-model="cooperDiscount"
                 name="cooperDiscount"
                 type="number"
                 label="折扣"
                 placeholder="请输入折扣"
                 :rules="[{ required: true, message: '请填写请输入折扣' }]" />
      <van-field v-model="dataForm.accountLimit"
                 name="accountLimit"
                 type="number"
                 label="挂账额度"
                 placeholder="请输入挂账额度" />
      <van-field readonly
                 clickable
                 name="billCycle"
                 :value="billCycle"
                 label="结账周期"
                 @click="showPicker = true"
                 placeholder="请选择结账周期" />
      <van-field readonly
                 style="margin-top: 13px;color: #000000"
                 label="财务信息" />
      <van-field v-model="dataForm.bankAccount"
                 name="bankAccount"
                 label="银行账户"
                 placeholder="选填" />
      <van-field v-model="dataForm.bankName"
                 name="bankName"
                 label="开户行"
                 placeholder="选填" />
      <van-field v-model="dataForm.taxAccount"
                 name="taxAccount"
                 label="税号"
                 placeholder="选填" />
      <van-field v-model="dataForm.location"
                 name="location"
                 label="公司地址"
                 placeholder="选填" />

      <div style="margin: 16px;">
        <van-button class="submit-btn"
                    block
                    native-type="submit">
          确认创建
        </van-button>
      </div>
    </van-form>

    <van-popup v-model="showPicker"
               position="bottom">
      <van-picker show-toolbar
                  :columns="columns"
                  @confirm="onConfirm"
                  @cancel="showPicker = false" />
    </van-popup>
  </div>
</template>

<script>
import Vue from 'vue';
import { Stepper } from 'vant';
import { GetSystemDictDataByType } from '@/api/api_query';
import { PostBusinessCooperationAdd } from '@/api/api_business';
import { Toast, Icon } from 'vant';
Vue.use(Stepper);
Vue.use(Toast);
export default {
    name: 'add',
    data() {
        return {
            stepper: 1,
            showPicker: false,
            dataForm: {
                cooperationName: null,
                hotelId: null,
                cooperationSn: null,
                contact: '',
                phone: '',
                cooperDiscount: 1,
                billCycle: '1',
                cooperType: '1',
                accountLimit: '',
                bankAccount: '',
                taxAccount: '',
                location: '',
                bankName: ''
            },
            columns: [],
            columnsAll: []
        };
    },
    created() {
        this.$store.commit('setHeader', '添加二房东');
        this.dataForm.hotelId = this.$route.query.hotelId;
        this.getTypeBusinessBill();
    },
    computed: {
        cooperDiscount: {
            get() {
                return this.dataForm.cooperDiscount * 10;
            },
            set(val) {
                this.dataForm.cooperDiscount = val / 10;
            }
        },
        billCycle: {
            get() {
                if (this.columnsAll && this.columnsAll.length) {
                    return this.columnsAll.find((item) => this.dataForm.billCycle == item.dictValue).dictLabel + ' ' + '＞';
                }
            },
            set(val) {}
        }
    },
    methods: {
        //查询字典
        getTypeBusinessBill() {
            GetSystemDictDataByType({ dictType: 'business_bill_cycle' }).done((res) => {
                this.dataForm.billCycle = res.data[0].dictValue;
                this.columns = res.data.map((item) => item.dictLabel);
                this.columnsAll = res.data;
            });
        },
        //提交表单
        onSubmit() {
            PostBusinessCooperationAdd(this.dataForm).done(() => {
                Toast.success('操作成功');
                setTimeout(() => {
                    this.$router.go(-1);
                }, 2000);
            });
        },
        //表单选择
        onConfirm(val) {
            this.dataForm.billCycle = this.columnsAll.find((item) => item.dictLabel == val).dictValue;
            this.showPicker = false;
        },
        validator(val) {
            return /^1[3-9][0-9]\d{8}$/.test(val);
        },
        validatorEmail(val) {
            return /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/.test(val);
        }
    }
};
</script>
<style scoped>
.group {
    height: calc(100vh - 8%);
    overflow-y: scroll;
}
.submit-btn {
    background: #ff7d41;
    color: #fff;
}
/deep/ .van-field__label {
    text-align: left;
}
</style>



